<template>
  <div id="dashboard">
      <ul class="collection with-header">
    <li class="collection-header">
      <h4>Employees</h4>
    </li>
    <li v-for="(data, k) in employees" :key="k" class="collection-item"> <div class="chip">{{data.department}}</div>{{data.employee_id}}: {{ data.name}}
    <router-link :to="{name:'view-employee', params:{employee_id:data.employee_id}}" class="secondary-content">
      <i class="fa fa-eye"></i>
    </router-link>
        
    </li>
    </ul>
    <div class="fixed-action-btn">
    <router-link to="/new" class="btn-floating btn-large red">
      <i class="fa fa-plus"></i>
    </router-link>
    </div>
  </div>
</template>
<script>
import db from '../firebase_services/db'
export default {
  name:'dashboard',
  data(){
  return{
employees:[]
  }
  },
  mounted(){
    db.collection('employees').orderBy('department').get().then(
    querySnapshot =>{
      querySnapshot.forEach(doc=>{
      // dimana data berada
      // kalo id nya ya di doc.id yang string beberapa panjang itu
      // console.log('Doc ',doc.data())
      const data = {
        'id' : doc.id,
        'employee_id': doc.data().id,
        'department': doc.data().department,
        'name': doc.data().name,
        'email': doc.data().email,
        'position': doc.data().position,
      }
      this.employees.push(data)
      })
    }
    )
  }
}
</script>